import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ProcurementPageComponent} from './procurement.component';

const routes: Routes = [
  {
    path: '',
    component: ProcurementPageComponent,
  },
  {
    path: 'form',
    loadChildren: () => import('./form-procurement/form-procurement.module')
      .then(module => module.FormProcurementPagePageModule)
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ProcurementPageRoutingModule { }
